  .card-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 200px;
    width: 120px;
    /* background-color: rgb(129, 179, 255); */
    margin: 10px;
  
    box-shadow: 0 1px 3px rgba(20, 0, 134, 0.12), 0 1px 2px ;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  }
  
  .card:hover {
    box-shadow: 0 14px 20px rgba(0, 119, 255, 0.514), 0 10px 10px ;
  }
  
  .img-wrapper {
    width: 110px;
    height: 90px;
    text-align: center;
    padding: 5px;
  }
  
  .img {
    max-width: 100%;
    max-width: 100%;
  }
  
  .skill-title-wrapper {
    padding: 5px;
    height: 100px;
    width: 110px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-end;
  }
  
  h4 {
    margin: 0;
    font-size: 20px;
  }

  .r180{
    transform: rotate(180deg) translateY(40px);
  }