.navbar {
    overflow: hidden;
    background-color: #333 90%;
    position: fixed;
    top: 0;
    width: 100%;
  }
  
  .navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .navbar a:hover {
    background: rgb(141, 191, 248);
    color: rgb(0, 42, 180);
  }